
header {
  font-size: 18px;
  line-height: 24px;
  background-color: var(--accent);
}
.header-top {
  background-color: #293449;
  min-height: 42px;
}
.main-header {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  color: white;
  width: 960px;
  margin: 0 auto;
  padding: 0 10px;
}
.header-logo {
  box-shadow: inset 0 -5px 0 rgba(0, 0, 0, 0.24);
  height: 47px;
  width: 220px;
  padding-top: 13px;
  margin-bottom: -5px;
  text-align: center;
  background-color: #ee3643;
}
.poisk {
  position: relative;
  width: 270px;
  height: 42px;
  color: white;
}
.poisk::before {
  content: "";
  position: absolute;
  height: 17px;
  top: 12px;
  left: 17px;
  width: 17px;
  background: url("/src/img/icon-search.png") no-repeat;
}
.poisk-input {
  padding-top: 12px;
  padding-bottom: 12px;
  font-size: 16px;
  margin-left: 45px;
  background: transparent;
  border: none;
  color: white;
}
input[type="text"]::-webkit-input-placeholder {
  color: white;
}
input[type="text"]::-moz-placeholder {
  color: white;
}
.zakaz {
  background: #63a63e;
}
.header-item {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
}
.header-item li {
  height: 42px;
  padding-top: 8px;
}
.nav-button {
  padding-left: 36px;
  padding-right: 22px;
  font-size: 17px;
  font-weight: 400;
  position: relative;
}
.nav-button1::before {
  content: "";
  top: 2px;
  left: 14px;
  width: 14px;
  height: 16px;
  position: absolute;
  background: url("/src/img/icon-bookmark.png");
}
.nav-button2::before {
  content: "";
  top: 2px;
  left: 12px;
  width: 15px;
  height: 15px;
  position: absolute;
  background: url("/src/img/cart-icon.png");
}
.nav-button__green {
  padding: 9px 13px;
}

