html {
  background-color: white;
  height: 100%;
}
*,
::after,
::before {
  box-sizing: border-box;
}
@font-face {
  font-family: Cuprum;
  /* Гарнитура шрифта */
  src: url("/fonts/Cuprum-Regular.ttf");
  /* Путь к файлу со шрифтом */
}
body {
  min-width: 960px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Cuprum", "Tahoma", sans-serif;
  display: grid;
  grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
}
button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1.15;
  /* 1 */
  margin: 0;
  /* 2 */
}
